@import "vars";

.survey-progress {
  position: fixed;
  top: 0;
  left: 0;
  width: 100% !important;
  background-color: $button-color;
  z-index: 2147483647;
  .survey-progress-bar {
    height: 0.5em;
    background-color: $progress-bar-color;
    span {
      display: none;
    }
    -webkit-transition: width 1s;
    transition: width 1s;
  }
}
@import "vars";
@import "buttons";
@import "fonts";
table#complete-table {
    p {
        padding: 0px;
        margin: 0px;
        padding-left: $general-spacing;
        padding-right: $general-spacing;
    }
    p.handwriting {
        line-height: 1;
        span {
            font-family: "jellyka", "Open Sans", sans-serif;
            font-size: 9em;
        }
        @media screen and (max-width: $break-small) {
            span {
                font-size: 7em;
            }
        }
    }
    span.calltoaction {
        font-family: "latolight";
        font-size: 2.5em;
        span.win {
            color: green;
            font-family: "latobold";
        }
        @media screen and (max-width: $break-small) {
            font-size: 2em;
        }
    }
    span.logo {
        img {
            opacity: 0.2;
            height: 3.5em;
        }
    }
    a.calltoaction {
        @extend .lightgreenbutton;
        margin: ($general-spacing * 2);
    }
}
@import "fonts";

$question-font-size: 1.1em;
$question-line-height: 1.5em;
$general-spacing: 0.5em;

$button-color: #f4f8fd;
$go-button-color: #227c69;
$button-border-color: #ccc;
$button-text-color: #478FE2;
$button-background-color: #ffffff;

$border-radius: 0.2em;

$break-small: 400px;
$break-large: 1200px;

$progress-bar-color: #9cb0d1;

$light-text-color: #444444;
$question-text-color: #000000;
$heading-font-size: 1.4em;

$faintfont: "latohairline", "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
$lightfont: "latolight", "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
$heavierfont: "latoregular", "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;

$padding-between-questions: 4em;
@import "vars";
@import "buttons";
@import "progress";
@import "fonts";
@import "surveycomplete";
html {
    height: 100%;
    body {
        height: 100%;
        -webkit-text-size-adjust: 100% !important;
        table.centering-table {
            width: 100%;
            height: 100%;
            td.centering-td {
                text-align: center;
            }
        }
        #surveyElement {
            font-family: $lightfont;
            max-width: 45em;
            font-size: $question-font-size;
            line-height: $question-line-height;
            margin-bottom: $general-spacing;
            -webkit-font-smoothing: subpixel-antialiased !important;
            padding-top: 5em;
            div.radio {
                margin-right: $general-spacing;
                display: inline-block;
            }
            .instructions {
                font-family: $faintfont;
                color: $light-text-color;
            }
            .rating,
            .radio,
            .checkbox {
                label.btn.btn-default {
                    font-size: 1.5em;
                    color: $button-text-color;
                    padding-left: 12px;
                    display: inline-block;
                    font-family: $heavierfont;
                    vertical-align: text-top;
                    @media screen and (max-width: $break-small) {
                        font-size: 1.1em;
                    }
                    span {
                        font-size: 0.7em;
                        position: relative;
                        top: -2px;
                        white-space: pre-wrap;
                    }
                }
            }
            .rating {
                label.btn.btn-default {
                    @media screen and (max-width: $break-small) {
                        padding: 6px 10px;
                    }
                }
            }
            .checkbox {
                label.btn.btn-default {
                    span {
                        @media screen and (max-width: $break-small) {
                            width: 20em !important;
                        }
                    }
                }
            }
            .checkbox.colCount4 {
                label.btn.btn-default {
                    span {
                        width: 6.6em;
                    }
                }
                .otherCb {
                    input[type=text] {
                        width: 9.5em;
                    }
                }
            }
            .checkbox.colCount3 {
                label.btn.btn-default {
                    span {
                        width: 10em;
                    }
                }
                .otherCb {
                    input[type=text] {
                        width: 13.4em;
                    }
                }
            }
            .checkbox.colCount2 {
                label.btn.btn-default {
                    span {
                        width: 16em;
                    }
                }
                .otherCb {
                    input[type=text] {
                        width: 20.4em;
                    }
                }
            }
            .checkbox.colCount1 {
                label.btn.btn-default {
                    span {
                        width: 34em;
                    }
                }
                .otherCb {
                    input[type=text] {
                        width: 41.1em;
                    }
                }
            }
            .checkbox {
                vertical-align: top;
                label.btn.btn-default {
                    margin-right: $general-spacing;
                    margin-bottom: $general-spacing;
                    span {
                        display: inline-block;
                        text-align: left;
                    }
                }
                .otherCb {
                    input[type=text] {
                        min-width: 15.6em;
                        @media screen and (max-width: $break-small) {
                            width: 18.8em !important;
                        }
                    }
                }
            }
            .panel-body,
            .panel-footer {
                padding-top: 0;
                padding-bottom: 0;
                padding-left: 3em;
                padding-right: 3em;
                @media screen and (max-width: $break-small) {
                    padding-left: 1.6em;
                    padding-right: 1.0em;
                }
            }
            .qtitle {
                font-size: $heading-font-size;
                position: relative;
                .qNum {
                    position: absolute;
                    left: -3em;
                    top: 0;
                    width: 2em;
                    text-align: right;
                    color: lighten($question-text-color, 70%);
                    @media screen and (max-width: $break-small) {
                        position: static;
                        left: 0;
                        top: 0;
                        margin-right: 0.5em;
                    }
                    &::after {
                        content: "›";
                        position: absolute;
                        top: 0;
                        right: -0.5em;
                        color: lighten($button-text-color, 30%);
                        @media screen and (max-width: $break-small) {
                            position: static;
                            left: 0;
                            top: 0;
                            padding-left: 0.2em;
                        }
                    }
                }
            }
            .form-inline {
                @media screen and (max-width: $break-small) {}
            }
            .question {
                margin-top: 1em;
                margin-bottom: $padding-between-questions;
                b {
                    font-weight: normal;
                    font-family: $heavierfont;
                }
                h1 {
                    font-family: $heavierfont;
                    font-size: $heading-font-size;
                    font-weight: normal;
                    color: $question-text-color;
                    position: relative;
                    &::before {
                        position: absolute;
                        content: "※";
                        font-size: 1.5em;
                        top: -0.17em;
                        left: -1em;
                        color: lighten($question-text-color, 70%);
                        font-weight: lighter;
                        @media screen and (max-width: $break-small) {
                            font-size: 0.8em;
                            top: 0.22em;
                            left: -0.95em;
                            color: lighten($question-text-color, 80%);
                        }
                    }
                }
                .radio input[type=radio],
                .checkbox input[type=checkbox] {
                    display: none;
                }
                .radio,
                .checkbox {
                    width: auto !important;
                    label {}
                }
            }
            .panel-footer {
                background-color: transparent;
                text-align: left;
                padding-top: $general-spacing !important;
                margin-bottom: $padding-between-questions;
                .navButton {
                    font-size: 1.1em;
                    font-family: $heavierfont;
                    &.complete,
                    &.next {
                        background-color: $go-button-color;
                    }
                }
            }
        }
    }
}